:root {
  --bg-color: #020b12;
  --second-bg-color: #000;
  --text-color: #fff;
  --second-text-color: #9093b1;
  --social-icon-color: #fff;
}

* {
  box-sizing: border-box;
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
}

a {
  color: currentColor;
