}

.video-text {
  height: 100%;
  width: 100%;
  color: #fff;
  mix-blend-mode: multiply;
  background-color: #000;
  justify-content: center;
  align-items: center;
  font-size: 100px;
  font-weight: 900;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.click-container {
  flex-direction: column;
  align-items: center;
  gap: 8px;
  display: flex;
  position: absolute;
  top: 85%;
  left: 50%;
}

.banner-text {
  cursor: pointer;
  color: var(--text-color);
  border: 0;
  font-family: Outfit;
  font-size: 30px;
}

.banner-text:after {
  content: "";
  height: 2px;
  width: 0%;
  background-color: var(--text-color);
  transition: all .5s ease-in-out;
  display: block;
}

.banner-text:hover:after {
  content: "";
  height: 2px;
  width: 100%;
  background-color: #5577fe;
  display: block;
}

.arrow-down {
  z-index: 2;
  margin: 30px;
}

.arrow-down span {
  width: 30px;
  height: 30px;
  border-bottom: 5px solid #1e34ff;
  border-right: 5px solid #ff2c2c;
  margin: -16px;
  animation: arrow-down 2s infinite;
  display: block;
  transform: rotate(45deg);
}

.arrow-down span:nth-child(2) {
  animation-delay: -.2s;
}

.arrow-down span:nth-child(3) {
  animation-delay: -.4s;
}

@keyframes arrow-down {
  0% {
    opacity: 0;
    transform: rotate(45deg)translate(-20px, -20px);
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    transform: rotate(45deg)translate(20px, 20px);
  }
}

.page-header {
